@import url(https://fonts.googleapis.com/css2?family=Exo+2:ital,wght@0,400;0,500;1,400&display=swap);
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.input-box{
    width: 100%;
    height: 80px;
    padding: 10px;
}
.input-box input{
    width: 70%;
    font-weight: 700;
}
.input-box input:focus{
    box-shadow: none;
}
.input-box button{
    width: 15%;
}
img{
    width: 40px;
    margin: 0 20px;
}
li{
    font-weight: 700;
}
i{
    cursor: pointer;
    font-size: 1.2rem;
}
